export const CONSTRUCTOR = 1;
export const ARCHITECT = 2;

export function findName(value) {
    // eslint-disable-next-line default-case
    switch (value) {
        case CONSTRUCTOR: return 'Nh Thu'
        case ARCHITECT: return 'Kin trc s'
    }
}

export function findColor(value) {
    // eslint-disable-next-line default-case
    switch (value) {
        case CONSTRUCTOR: return '#20c997'
        case ARCHITECT: return '#dc3535'
    }
}